*{
  padding: 0;
  margin:0;
  box-sizing: border-box;
}
body{
  color: #334D6E;
  font-family: 'Poppins', sans-serif;
  height: auto;


}
.Mainjob{
  background-color:white ;
  height: 100vh;
  width: 100%;
  display: flex;
position: relative;
}
.logobox_Main{
  width: 17%;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  background-color: whitesmoke;
  flex-direction: column;
  box-shadow: 0 0 4px;
}
.logo{
  margin: 0;
 width: 100%;
 display: flex;
 align-items: center;
 justify-content: center;
 height: 70px;
 background-color: #fff;
}
.user_Main{
  margin: 20px;
}
.user_Main h3,.user_Main p{
  margin: 0;
}
.NavContainer_Main{
width: 83%;
height: auto;
position:absolute;
left: 17%;
}
.navbar_Main{
height: 50px;
width: 82.5%;
position: fixed;
left: 17.1%;
z-index: 1;
display: flex;
box-shadow: 0 1px 4px grey;
background-color: whitesmoke;
align-items: center;
justify-content: flex-end;

}
.list{
  display: flex;
 
}

.list li {
margin: 0 50px 0 10px;
  list-style: none;

 
}
.list li a{
  text-decoration: none;
  color: #192A3E;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.list li a .fas,.list li a .far{
padding-right: 5px;
}

.whole{
  width: 100%;
  position: absolute;
  top: 58px;
  
}

.Main_btns{
margin: 10px 30px;
display: flex;
align-items: center;
justify-content: space-between;

}
.job_intrv_btns{
  display: flex;
  align-items: center;
}
.containermain p{
  padding-left:15px ;
}
.jobbtn,.crtbtn,.intrbtn{
  padding: 6px;
  border: none;
  font-family: 'Poppins', sans-serif;
  font-size: 12px;
  background-color: #e5eff5;
  cursor: pointer;
  border-radius: 4px;
  color: #192A3E;
}
.jobbtn{
margin-left: 5px;
width: 85px;
cursor: pointer;
}
.intrbtn{
margin-left: 10px;
width: 115px;
cursor: pointer;
}
.sort{
  margin: 10px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.507);
}
.crtbtn{
  font-size: 15px;
  background-color: #06be37;
  color: white;
  cursor: pointer;
}

.containermain{
  width: 95%;
  height: 100vh;
background-color: whitesmoke;
margin: 20px auto;
  border: 0.1px solid rgba(128, 128, 128, 0.24);
  box-shadow: 0 0 4px;
 
}

.alljobsdiv{
  margin:10px 0 ;
  line-height: 2em;
}
.alljobsdiv b{
  cursor: pointer;
}
.alljobs{
  margin: 10px 0;
}
.fa-user-tie,.fa-pencil-alt,.fa-ban{
  padding-right: 7px;
  color: rgba(59, 59, 59, 0.568);
  cursor: pointer;
}
.leftbtns_Main{
  margin: 40px 0 10px 0;
}

.Emailbtn_Main{
  width: 160px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #008BDF;
  color: #fff;
  border: none;
  margin: 13px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}



.Supportbtn_Main{
  width: 160px;
  cursor: pointer;
  background-color: #c4dceb;
  color: #192A3E;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 13px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;}
.createbtn{
 
  
  background-color: #06be37;
  border-radius: 50%;
  margin: 0 10px -7px 10px;
  cursor: pointer;
 
  color: #fff;
}

.rtARwBtn{
  display: flex;
  align-items: center;
 margin-right: 10px;
}
.jobnames{
 margin:20px 10px;

}
.Starthiringbtn{
  width: 150px;
  margin:0 20px 0 40px;
  cursor: pointer;

  background-color: #008BDF;
  color: #fff;
  border: none;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}
.All{
 display: flex;
 align-items: center;
 justify-content: space-between;
  border-bottom: 1px solid rgba(128, 128, 128, 0.712);
}


